import * as React from 'react'
import './chart.scss'
import * as d3 from 'd3'
import { useRef } from 'react'
import { tempData } from './data'

export interface ChartProps {
    data?: any
}

const Chart: React.FC<ChartProps> = props => {
    const d3Container = useRef(null)

    const response = tempData
    console.log('data', response)

    if (response) {
        let padding = {
            top: 100,
            bottom: 100,
            left: 100,
            right: 100,
        }
        let graphWidth = 700
        let graphHeight = 400
        let svgWidth = graphWidth + padding.left + padding.right
        let svgHeight = graphHeight + padding.top + padding.bottom
        let legendWidth = 200
        let legendHeight = 30
        const svg = d3
            .select('body')
            .append('svg')
            .attr('width', svgWidth)
            .attr('height', svgHeight)

        let toolTip = d3
            .select('body')
            .append('div')
            .attr('id', 'tooltip')
            .style('display', 'none')
            .style('opacity', '0')

        let xMin = d3.min(response.monthlyVariance, (d: any) => {
            return new Date(d.year, 0) as any
        })

        let xMax = d3.max(response.monthlyVariance, (d: any) => {
            return new Date(d.year, 0) as any
        })

        let xScale = d3
            .scaleTime()
            .domain([xMin as any, xMax])
            .range([padding.left, padding.left + graphWidth])
        let xAxis = d3.axisBottom(xScale)

        let domain
        let yMax
        let yScale = d3
            .scaleBand()

            .domain([
                'January',
                'February',
                'March',
                'April',
                'May',
                'June',
                'July',
                'August',
                'September',
                'October',
                'November',
                'December',
            ])
            .range([padding.left + graphHeight, padding.left])

        let yAxis = d3.axisLeft(yScale)

        let baseTemp = response.baseTemperature
        let hMin = d3.min(response.monthlyVariance, (d: any) => {
            let temp = baseTemp + d.variance
            return temp
        })
        let hMax = d3.max(response.monthlyVariance, (d: any) => {
            let temp = baseTemp + d.variance
            return temp
        })
        let mediumHeat = ((hMin as any) + hMax) / 2
        let colors = ['#000066', '#FF0000']
        let colorScale = d3
            .scaleLinear()
            .domain([hMin as any, hMax])
            .range([colors[0] as any, colors[1]])

        let legendData = [
            hMin,
            ((mediumHeat as any) + hMin) / 2,
            mediumHeat,
            ((mediumHeat as any) + hMax) / 2,
            hMax,
        ]

        svg.append('g')
            .call(xAxis)
            .attr('transform', `translate(0,${graphHeight + padding.top})`)
            .attr('id', 'x-axis')
            .append('text')
            .text('Years')
            .style('text-anchor', 'middle')
            .style('font-size', '1rem')
            .style('font-weight', 'bold')
            .attr('transform', `translate(${svgWidth - 150},45)`)
            .attr('fill', 'grey')

        svg.append('g')
            .call(yAxis)
            .attr('transform', `translate(${padding.left},0)`)
            .attr('id', 'y-axis')
            .append('text')
            .text('Months')
            .style('text-anchor', 'middle')
            .style('font-size', '1rem')
            .style('font-weight', 'bold')
            .attr('transform', `translate(-70,150) rotate(-90)`)
            .attr('fill', 'grey')

        let cells = svg
            .selectAll('rect')
            .data(response.monthlyVariance)
            .enter()
            .append('rect')
            .attr('width', 5)
            .attr('height', 30)
            .attr('x', (d: any) => xScale(new Date(d.year, 0)) + 2)
            .attr('y', (d: any) => {
                const date = new Date(1970, d.month - 1) as any

                let options = { month: 'long' }
                let month = new Intl.DateTimeFormat('en-US', options).format(date as any)

                return yScale(month) as any
            })
            .attr('fill', (d: any, i) => {
                return colorScale(baseTemp + d.variance)
            })
            .attr('class', 'cell')
            .attr('data-month', (d: any) => d.month - 1)
            .attr('data-year', (d: any) => d.year)
            .attr('data-temp', (d: any) => d.variance)
            .on('mouseover', function(d: any) {
                let date = new Date(1970, d.month - 1)

                let options = { month: 'long' }
                let month = new Intl.DateTimeFormat('en-US', options).format(date)
                toolTip
                    .style('display', 'flex')
                    .style('opacity', '0.9')
                    .html(`${d.year}-${month}: ${Math.round(d.variance + baseTemp)}℃`)
                    .style('left', d3.event.pageX + 5 + 'px')
                    .style('top', d3.event.pageY - 25 + 'px')
                    .attr('data-year', d.year)
            })
            .on('mouseout', function(d) {
                toolTip.style('display', 'none').style('opacity', '0')
            })

        let title = svg
            .append('text')
            .text('Monthly global ground temp')
            .attr('id', 'title')
            .attr('x', 280)
            .attr('y', 30)

        let description = svg
            .append('text')
            .text('1753 - 2015')
            .attr('id', 'description')
            .attr('x', 300)
            .attr('y', 60)

        let legend = svg
            .append('g')
            .attr('id', 'legend')
            .selectAll('rect')
            .data(legendData)
            .enter()
            .append('rect')
            .attr('width', d => legendWidth / 4)
            .attr('height', legendHeight)
            .attr('fill', (d: any) => colorScale(d))
            .attr('x', (d, i) => padding.left + (legendWidth / 4) * i)
            .attr('y', svgHeight - padding.bottom / 1.5)

        let legendTicks = svg
            .append('g')
            .selectAll('text')
            .data(legendData)
            .enter()
            .append('text')
            .text((d: any) => '~' + Math.round(d) + '℃')
            .attr('x', (d, i) => padding.left + (legendWidth / 4) * i + 10)
            .attr('y', svgHeight - padding.bottom / 2)
            .style('font-size', '0.7rem')
    }

    return <main id="main" ref={d3Container}></main>
}

export default Chart
